RME.component({ routers: () => ({
    'div.w3-container': [
        {h1: () => 'Router explained'},
        {'component:text': {text:
            `Router class takes a root element in which it will render other route elements. Using the Router is relatively simple but there are couple of things to mention about.
            The Router has two implementations from which one implementation has to be choosen. One implementation is a hash based and the other is an url based.`
        }},
        {paragraph: [
            {sline: {text: 'Route object has four properties '}},
            {bline: {text: 'route'}},{sline: {text: ', '}},
            {bline: {text: 'elem'}},{sline: {text: ', '}},
            {bline: {text: 'hide'}},{sline: {text: ' and '}},
            {bline: {text: 'scrolltop'}},{sline: {text: '.'}}
        ]},
        {code: {text:
'{\n\
    route: \'url\', *\n\
    elem: \'statelessComponentNameString|elemObject\', *\n\
    hide: true|false|undefined,\n\
    scrolltop: true|false|undefined\n\
}'
        }},
        {paragraph: [
            {iline: {
                text: 'Properties marked by asterix (*) are mandatory.'
            }}
        ]},
        {'ul.w3-large': [
            {li: [
                {S: {text: 'The '}},{B: {text: 'route'}},
                {S: {text: ' property is type of string and holds the route URL.'}}
            ]},
            {li: [
                {S: {text: 'The '}},{B: {text: 'elem'}},
                {S: {text: ' property is stateless component name or elem object. The property holds an object that will be rendered inside the root element.'}}
            ]},
            {li: [
                {S: {text: 'The '}},{B: {text: 'hide'}},
                {S: {text: ' property is type of boolean and determines if the URL is hidden or not. More about hiding the URL further down.'}}
            ]},
            {li: [
                {S: {text: 'The '}},{B: {text: 'scrolltop'}},
                {S: {text: ' property enables or disables auto-scroll behavior for a sigle route element.'}}
            ]}
        ]},
        {paragraph: [
            {B: {text: 'Do not use statefull components direcly as route elements. '}},
            {S: {text: 'Statefull components may be a child component of stateless route element component.'}}
        ]},
        {tptext: {text: 
            'Illustration of the route object.'
        }},
        {code: {text:
'{route: \'/error\', elem: \'errorPage\', hide: true}'
        }},
        {'component:text': {text: 
            `Router.routes() function will take an array of route objects that will define the routes that the Router listens. If a root is not set, then by
            default the first index (0) of the array of route objects will be set as the root object in which all other route objects are rendered.`
        }},
        {tptext: {text: 
            'Following code clip illustrates setting up routes and using the Router.route function.'
        }},
        {code: {text:
'Router.url().routes([\n\
    {elem: \'#root\'},\n\
    {route: \'page1Url\', elem: \'page1\'}\n\
]);'
        }},
        {tptext: {text: 
            'An alternative way to create routes.'
        }},
        {code: {text:
'Router.url().root(\'#rootApp\').routes([\n\
    {route: \'page1Url\', elem: \'page1Comp\'}\n\
]);'
        }},
        {'component:text': {text: 
            `The URL based and the hash based routing is generally supported. But manual URL based routing is only supported on HTML5 and newer.
            This is because the manual URL based routing uses features that came along with the HTML5. If the manual URL based routing is used then
            it will be also possible to hide the URL from the browsers address bar as mentioned below. In such cases the manual URL routing can be used in
            earlier versions of the HTML because when the URL is hidden then it does not use the HTML5 functionalities to control the history state of a webpage.`
        }},
        {subTitle: {text: 'Scroll behavior'}},
        {paragraph: [
            {sline: {text: 
                'Router has a function scroll(auto) that takes a boolean value which determines auto-scroll top default behavior. The '
            }},
            {bline: {text: 'default'}},
            {sline: {text: ' is '}},
            {bline: {text: 'true'}},
            {sline: {text: 
                '. The default can be changed using the function. And the default behavior can be overriden for a single route element.'
            }}
        ]},
        {tptext: {text: 
            'If wanted we could disable the default auto-scroll top behavior as follows.'
        }},
        {code: {text:
'Router.scroll(false)....'
        }},
        {tptext: {text: 
            'If wanted we could disable the auto-scroll top behavior for a single element as follows.'
        }},
        {code: {text:
'{route: \'url\', elem: \'component\', scrolltop: false}"'
        }},
        {subTitle: {text: 'Not found behavior'}},
        {paragraph: [
            {sline: {text: 'Router also supports '}},
            {bline: {text: 'not found'}},
            {sline: {text: ' behavior through wild card syntax. The not found page is good to place the last because as it will match the rest possible cases.'}}
        ]},
        {tptext: {text: 
            'Following route will be the last match if nothing else matches before this when placed as the last route in the routes array.'
        }},
        {code: {text: 
'{route: \'*\', elem: \'notFound\'}'
        }},
        {subTitle: {text: 'URL based routing'}},
        {'component:text': {text: 
            `The URL based routing defaults to the HTML feature that when pressing a link it will reload a new page. Thus default URL based routing functionality listens the url change on page load.
            This suits well for static webpage environment. But the default functionality of links are not enough nor suitable to every place. The URL based routing
            also has support for such cases.`
        }},
        {tptext: {text: 
            'To set up a default URL based routing is done as illustrated below.'
        }},
        {code: {text:
'Router.url().root(\'#root\').routes([\n\
    {route: \'/\', elem: \'home\'},\n\
    {route: \'/page1\', elem: \'page1\'}\n\
]);'
        }},
        {tptext: {text: 
            'To set up a single page application fashion URL based routing is done as illustrated below.'
        }},
        {code: {text:
'Router.url(true).root(\'#root\').routes([\n\
    {route: \'/\', elem: \'home\'},\n\
    {route: \'/page1\', elem: \'page1\'}\n\
]);'
        }},
        {paragraph: [
            {sline: {text: 'When a boolean parameter of the Router.url() function is set to'}},
            {iline: {text: ' true, '}},
            {sline: {text: `then the Router uses a manual URL routing. When the manual routing is used then the user must also provide a way to
            invoke a Router.navigate() function that will navigate to a next view. The Router.navigate() function has couple of parameter combinations which enables
            a simple usage. The first parameter of the function may either be a string or an Event. If two parameters are given then they must be an Elem and an Event.`}}
        ]},
        {'component:text': {text: 
            `The URL based routing also supports hiding the URL. This might be useful on some cases but if the URL is hidden, it wont be saved into the browser history either.
            This means that it wont be possible to go back to the hidden URL page by pressing the back or the forward buttons of the browser.
            Hidden URL based routing is only possible when the manual url based routing is used.`
        }},
        {tptext: {text: 
            'The following example shows how to create hidden routes.'
        }},
        {code: {text:
'Router.url(true).root(\'#root\').routes([\n\
    {route: \'/\', elem: \'home\'},\n\
    {route: \'/page1\', elem: \'page1\'},\n\
    {route: \'/page2\', elem: \'page2\', hide: true},\n\
    {route: \'/error\', elem: \'err\', hide: true}\n\
]);'
        }},
        {'component:text': {text: 
            'Moreover the URL based routing supports a wildcard URL routing. A wildcard is marked with an asterix (*) and it stands for any number of any character.'
        }},
        {tptext: {text: 
            'The wildcard URL route illustrated as follows.'
        }},
        {code: {text:
'Router.url(true).root(\'#root\').routes([\n\
    {route: \'/\', elem: \'home\'},\n\
    {route: \'/page/*/info/*\', elem: \'page\'}\n\
]);'
        }},
        {tptext: {text: 
            'Assuming that the manual URL based routing is used, an example menu could be as follows.'
        }},
        {code: {text:
'let menu = Template.resolve({\n\
    div: [\n\
        {a: {href: \'/\', text: \'Home\', onClick: Router.navigate}},\n\
        {br: {}},\n\
        {a: {href: \'/page1\', text: \'Page1\' onClick: Router.navigate}},\n\
        {br: {}},\n\
        {a: {href: \'/page2\', text: \"Page2\", onClick: Router.navigate}}\n\
        {br: {}},\n\
        {a: {href: \'/page/settings/info/user\', text: \'Page3\', onClick: Router.navigate}}\n\
    ]\n\
});'
        }},
        {paragraph: [
            {sline: {text: 'If the default URL based '}},{bline: {text: '(not manual) '}},
            {sline: {text: `routing is used then the custom onClick should not be used as the routing happens on page load.`}}
        ]},
        {subTitle: {text: 'Hash based routing'}},
        {'component:text': {text: 
            `The hash based routing is much more simple and straightforward to set up. To set it up just invoke a Router.hash() to choose the hash based routing.
            No other configuration is needed. The hash based routing automatically listens hash change. You should not manually use a Router.navigate() when
            the hash based routing is used as it will cause double rendering.`
        }},
        {tptext: {text: 
            'Following clip illustrates how to create the hash based routing.'
        }},
        {code: {text:
'Router.hash().root(\'#root\').routes([\n\
    {route: \'home\', elem: \'home\'},\n\
    {route: \'page1\', elem: \'page1\'},\n\
    {route: \'page2\', elem: \page2\'}\n\
]);'
        }},
        {'component:text': {text: 
            `After the routing is set up, then only thing left is to create a menu that corresponds to the routes.
            One thing to remember is that when the hash based routing is used then links that are ought to be routed should also start with hashtag.`
        }},
        {tptext: {text: 
            'Next segment creates a menu that has three links.'
        }},
        {code: {text:
'let menu = Template.resolve({\n\
    div: [\n\
        {a: {href: \'#home\', text: \'Home\'}},\n\
        {br: {}},\n\
        {a: {href: \'#page1\', text: \'Page1\'}},\n\
        {br: {}},\n\
        {a: {href: \'#page2\', text: \'Page2\'}}\n\
    ]\n\
});'
        }},
        {'component:text': {text: 
            `Hidden hash based routing is possible to but there is one thing to remember for such case. Hidden hash routes has to be manually navigated by
            invoking the Router.navigate() function.`
        }},
        {tptext: {text: 
            "Basic implementation same as before except the hidden hash route."
        }},
        {code: {text:
'Router.hash().root(\'#root\').routes([\n\
    {route: \'home\', elem: \'home\'},\n\
    {route: \'page1\', elem: \'page1\'},\n\
    {route: \'page2\', elem: \'page2\'}\n\
    {route: \'hidden\', elem: hiddenElem, hide: true}\n\
]);'
        }},
        {tptext: {text: 
            'Menu implementation is the same except the hidden hash route has to invoke the Router.navigate function as follows.'
        }},
        {code: {text:
'let menu = Template.resolve({\n\
    div: [\n\
        {a: {href: \'#home\', text: \'Home\'}},\n\
        {br: {}},\n\
        {a: {href: \'#page1\', text: \'Page1\'}},\n\
        {br: {}},\n\
        {a: {href: \'#page2\', text: \'Page2\'}}\n\
        {br: {}},\n\
        {a: {href: \'#hidden\', text: \'Hidden\', onClick: Router.navigate}}\n\
    ]\n\
});'
        }}
    ]
})});