RME.component({ code: (props) => ({
    'div.w3-container.w3-light-gray.w3-padding.code': {text: props.text}
})});

RME.component({ dcode: (props) => {
    let pages = [
        RME.component('code', {text: props.es6}),
        RME.component('code', {text: props.es5})
    ];
    const change = (tab, idx) => {
        tab.parent().getFirst('.w3-light-gray').removeClasses('w3-light-gray');
        tab.addClasses('w3-light-gray');
        tab.parent().next().render(pages[idx]);
    }
    return {
        div: {
            'div.w3-bar': [
                {tab: {
                    selected: true,
                    text: 'ES 6',
                    change: change,
                    idx: 0
                }},
                {tab: {
                    text: 'ES 5',
                    change: change,
                    idx: 1,
                }}
            ],
            div: (e) => e.append(pages[0])
        }
    }
}});

RME.component({ tab: (props) => ({
    'div.w3-bar-item.w3-button.w3-hover-light-gray': {
        class: props.selected ? 'w3-light-gray' : '',
        text: props.text,
        onClick: (event) => props.change(Elem.wrap(event.target), props.idx)
    }
})});