RME.component({ templates: () => ({
    'div.w3-container': [
        {h1: () => 'Templates explained'},
        {'component:text': {text: 
            `Template class reads a JSON format notation and creates an element tree from it.`
        }},
        {tptext: { text: 
            'Template.resolve() function will create an Elem instance element tree from a given JSON object.'
        }},
        {code: {text: 
            'Template.resolve({...});'
        }},
        {'component:text': {text: 
            'The template object has to have a one root element from where it starts creation of an element tree. If a root element is not found Template resolve will throw an exeption.'
        }},
        {tptext: {text:
            'An object notation supports basically all HTML 5 attributes plus some custom attributes. Some of the supported attributes are found below.'
        }},
        {code: {text:
'htmlTag: {\n\
    id: \'id\',\n\
    name: \'name\',\n\
    class: \'awesomeclass awesomefont\',\n\
    text: \'basic text\',\n\
    value: \'value\',\n\
    content: \'<p>html string/<p>\',\n\
    tabIndex: \'tabIndex\',\n\
    type: \'type\',\n\
    src: \'src\',\n\
    href: \'href\',\n\
    editable|contenteditable: \'true|false\',\n\
    placehoder: \'placeholder\',\n\
    size: \'size\',\n\
    checked: true|false,\n\
    disabled: true|false,\n\
    visible: true|false,\n\
    display: true|false,\n\
    draggable: true|false,\n\
    styles: {stylesObjectMap},\n\
    for: \'for\',\n\
    message: \'message.hello.world\',\n\
    target: e.g. \'_blank\',\n\
    title: \'title\',\n\
    autofocus: true|false,\n\
    plus many other attribute....\n\
}'
        }},
        {'component:text': {text: 
            `Limitations of the template are the limitations of the JSON. Since the template is defined in JSON format thus having couple of things
            to mention about.`
        }},
        {tptext: {text: 
            `The following code will only have a latter defined div element visibile and the first defined will be omitted. This is because
            JavaScript objects can oly have a single property of the same name.`
        }},
        {code: {text:
'Template.resolve({\n\
    div: {\n\
        div: {\n\
            text: \'this is omitted\'\n\
        },\n\
        div: {\n\
            text: \'this is shown\'\n\
        }\n\
    }\n\
});'
        }},
        {'component:text': {text:
            'In such cases you should place multiple same elements into an array of elements.'
        }},
        {tptext: { text:
            'Following code will maken element tree having root div that has two child divs.'
        }},
        {code: {text: 
"Template.resolve({\n\
    div: [\n\
        {div: {\n\
            text: \'the first child\'\n\
        }},\n\
        {div: {\n\
            text: \'the second child\'\n\
        }}\n\
    ]\n\
});"
        }},
        {'component:text': {text:
            'Sometimes this basic array object notation is just not enough. And which is why Template.resolve() supports comprehensive ways of defining an element tree.'
        }},
        {tptext: {text: 
            'Because arrays cannot support object notation. Properties, classes and an id could be given as string for the array definition as follows.'
        }},
        {code: {text: 
'Template.resolve({\n\
    \'div#id.class1.class2[attr1=vale][attr2=value]\': [\n\
        {div: {\n\
            class: \'awesome-class1\',\n\
            text: \'some text\'\n\
        }\n\
        ......\n\
    ]\n\
});'
        }},
        {'component:text': {text:
            'Sometimes it will be easier and necessary to have a function implementation for an element.'
        }},
        {tptext: { text: 
            'Function implementations support ES 6 and ES 5 notations.'
        }},
        {dcode: {
            es6:
'Template.resolve({\n\
    div: {\n\
        span: (e) => {\n\
            e.addClasses(\'awesome-class1 text-highlight\');\n\
            e.setText(\'text of the span\')\n\
        }\n\
    }\n\
});',
            es5: 
'//ES 5 functions can refer to \'this\' or an appropriate elem that is received as an attribute\n\
\n\
Template.resolve({\n\
    div: {\n\
        span: function(span) {\n\
            this.addClasses(\'awesome-class1 text-highlight\');\n\
            span.setText(\'text of the span\')\n\
        }\n\
    }\n\
});'
        }},
        {'component:text': {text: 
            'By convention you are able to return text from the function notation which will automatically set the text according to the returned text value.'
        }},
        {dcode: {
            es6: 
'Template.resolve({\n\
    div: {\n\
        span: (e) => {\n\
            e.addClasses(\'awesome-class1 text-highlight\');\n\
            return \'this is the text of the span\'\n\
        }\n\
    }\n\
});',
            es5:
'Template.resolve({\n\
    div: {\n\
        span: function() {\n\
            this.addClasses(\'awesome-class1 text-highlight\');\n\
            return \'this is the text of the span\'\n\
        }\n\
    }\n\
});'
        }},
        {tptext: {text: 
            'Which leads to highly short declaration if ES 6 and higher is used.'
        }},
        {code: {text: 
"Template.resolve({\n\
    div: {\n\
        \"span.awesome-class1\": => \"this is the text of the span\"\n\
    }\n\
});"
        }},
        {'component:text': {text: 
            `By convention returned string value is resolved and determined that is it a message key or a normal string. If the string is a message key and Messages are defined and in use then
            the key is ought to be resolved. If the key was not resolved then the key is returned. And if the string is not resolved as the message key then the string is directly
            inserted as text.`
        }},
        {tptext: {text: 'The function notation also supports returning a message key.'}},
        {code: {text: 
'Template.resolve({\n\
    div: {\n\
        \'span.awesome-font\': => \'front.page.welcome.user\'\n\
    }\n\
});'
        }},
        {subTitle: {text: 'Templates with components'}},
        {'component:text': {text: 
            `Components can be inserted inside the template easily and sometimes this will be the case. Reusable components inside the template makes creation of
            complex structure easier.`
        }},
        {tptext: {text: 
            'Assuming that we would have already created a following component named "customSpan" that takes one property named "customText". The component implements a span element.'
        }},
        {dcode: { 
            es6: 
'RME.component({ customSpan: props => ({\n\
    span: {text: props.customText}\n\
})});',
            es5: 
'RME.component({ customSpan: function(props) {\n\
    return { \n\
        span: {text: props.customText}\n\
    }\n\
}});'
        }},
        {'component:text': { text: 
            'To use the component with the template simply call the name of the component and give properties of the component inside an object.'
        }},
        {code: {text: 
'Template.resolve({\n\
    div: {\n\
        customSpan: {customText: \'this is the text of the component\'}\n\
    }\n\
});'
        }},
        {'component:text': {text: 
            'Other way of appending the component inside the template would be like this.'
        }},
        {dcode: {
            es6: 
'Template.resolve({\n\
    div: (e) => e.append({customSpan: {customText: \'this is the text of the component\'}});\n\
});',
            es5: 
'Template.resolve({\n\
    div: function() {\n\
        this.append({customSpan: {customText: \'this is the text of the component\'}});\n\
    }\n\
});'
        }},
        {paragraph: [
            {bline: {text: 
                "Please note that if the component name is either equal to HTML element or an object notation parameter name e.g. \"text\" then in order to use such component the component must be explicitly called using a \"component\" keyword as follows."
            }}
        ]},
        {tptext: {text: 
            "A component named \"text\" is explicitly called using the component keyword."
        }},
        {code: {text: 
"Template.resolve({\n\
    \"component:text\": {\n\
        text: \"Some text...\"\n\
    }\n\
});"
        }},
        {paragraph: [
            {sline: {text: "More information about components in: "}},
            {navLink: {
                text: "Components Explained",
                href: "/rme/components-explained"
            }}
        ]},
    ]
})});