RME.component({ quote: (props) => ({
    'div.w3-panel.w3-leftbar.w3-border-bottom.w3-light-gray.w3-margin': [
        {p: {
            class: 'w3-xlarge w3-serif',
            span: {
                class: 'w3-xxlarge',
                content: '&#10078;&nbsp;'
            },
            i: () => props.text
        }},
        {p: () => '-RME'}
    ]
})});

RME.component({ list: (props) => ({
    'ul.w3-ul': props.list.map(val => ({
        li: {
            a: {
                href: val.link,
                text: val.text,
                target: val.target
            },
            'span.w3-text-gray': {content: val.desc ? `&nbsp;${val.desc}` : ''}
        }
    }))
})});

RME.component({ line: () => ({ 'div.w3-border-bottom.line': {} })});

App.component({ dataTable: (props) => {
    let cont = Util.isEmpty(props.filtered) ? props.data : props.filtered;
    return {
        div: [
            props.data.length > 10 ? {'div.w3-row': {
                'input.w3-input.w3-xlarge.w3-border.w3-margin-top[placeholder=Type to Filter Table]': {
                    onInput: (event) => App.setState(props.ref, 
                        {filtered: RME.storage(props.key)
                            .filter(row => row.name.toLowerCase().search(event.target.value) > -1)}),
                    onBlur: () => App.clearState(props.ref)
                }
            }} : undefined,
            {'div.w3-row.w3-padding.w3-border-bottom.w3-text-teal': {
                'div.w3-container.w3-third': {b: () => 'Function'},
                "div.w3-container.w3-twothird": {b: () => 'Description'}
            }},
            {div: cont.map(val => ({
                'div.w3-row.w3-padding.data-table-row': {
                    'div.w3-container.w3-third': () => val.name,
                    'div.w3-container.w3-twothird': () => val.description
                }
            }))}
        ]
}} })();