RME.component({ storageEx: () => ({
    'div.w3-container': [
        {subTitle: {text: 'RME storage explained'}},
        {'component:text': {text: 
            `RME storage is a RME global instance storage in which data is stored using a appropriate method. 
            You should not confuse the RME storage with a Storage class that is the LocalStorage interface.`
        }},
        {'paragraph.w3-text-teal': [
            {sline: {text: 'Method saves a value'}},
            {iline: {text: ' bar '}},
            {sline: {text: 'with a key'}},
            {iline: {text: ' foo '}},
            {sline: {text: 'into the RME global storage.'}}
        ]},
        {code: {text:
'RME.storage(\'foo\', \'bar\');'
        }},
        {paragraph: {
            color: 'w3-text-teal',
            lines: [
                    {sline: {text: 'Method gets the saved '}},
                    {iline: {text: 'bar'}},
                    {sline: {text: ' value from the RME global instance storage with the key '}},
                    {iline: {text: 'foo'}},
                    {sline: {text: '.'}}
                ]
            
        }},
        {code: {text:
'RME.storage(\'foo\');'
        }},
        {'component:text': {text: 
            'Remember if different data is saved to the RME storage with same key then the data is overridden.'
        }},
        {'component:text': {text: 
            `Sometimes it might come very handy to be able to notify or do some specific action after new data is saved to the RME instance storage. 
            This is possible by creating a onStorageChange(..) listener function.`
        }},
        {tptext: {text: 
            'Following method is invoded if defined and it automatically gets new storage state as a parameter.'
        }},
        {dcode: {
            es6:
'RME.onStorageChange(newStorageState => {\n\
    //do some action after updating the storage.\n\
});',
            es5:
'RME.onStorageChange(function(newStorageState) {\n\
    //do some action after updating the storage.\n\
});'
        }},
    ]
})});

RME.component({ rmeEx: () => ({
    div: {
        'div.w3-container': [
            {h1: () => 'RME explained'},
            {paragraph: [
                {sline: {text: 'The RME offers functions such as component, script and storage. More about components in '}},
                {navLink: {href: '/rme/components-explained', text: 'Components Explained'}},
                {sline: {text: '. Information how an application should be started on RME version 1.2.0 onwards is here: '}},
                {navLink: {href: '/rme/app-explained', text: 'Application Explained'}},
                {sline: {text: '.'}}
            ]},
            {tptext: {text: 'Prior to RME version 1.2.0 the application was started using these two functions.'}},
            {'ul.w3-large': [
                {li: () => 'The RME.run() is run immediately when defined. If multiple run() functions are defined they are run in definition order.'},
                {li: () => 'The RME.ready() is run after the Body is ready. If multiple ready() functions are defined the latter definition is run.'}
            ]},
            {subTitle: {text: 'RME use explained'}},
            {paragraph: [
                {S: {text: 
                    `RME.use(..) function receives one configuration object that simply tells the RME to use
                    configured classes together. The configuration object has three optional parameters `}},
                    {B: {text: 'router'}},{S: {text: ', '}},{B: {text: 'messages'}},{S: {text: ' and '}},
                    {B: {text: 'app'}},{S: {text: '.'}},
            ]},
            {'ul.w3-large': [
                {li: {B: {text: 'router '}, S: {text: 'takes static Router reference.'}}},
                {li: {B: {text: 'messages '}, S: {text: 'takes static Messages reference.'}}},
                {li: {B: {text: 'app '}, S: {text: 'takes Application instance reference.'}}}
            ]},
            {tptext: {text: `Following code clip will configure the RME to use Router, Messages and a default Application instance together.`}},
            {code: {text: 
'RME.use({\n\
    router: Router,\n\
    messages: Messages,\n\
    app: App.get()\n\
});'
            }},
            {subTitle: {text: 'RME script explained'}},
            {'component:text': {text: 'RME script function imports an appropriate script file into the head. This is not needed when working on NPM based projects.'}},
            {tptext: {text: 'Following line would import a script file into the head.'}},
            {code: {text: 'RME.script(\'resources/myScript.js\')'}}
        ],
        storageEx: {}
    }
})});

RME.component({ httpEx: () => ({
    'div.w3-container': [
        {h1: () => "Http explained"},
        {'component:text': {text: 
            `Http is an interface that offers easy to use method to create asynchronous requests to server. The Http interface implements XMLHttpRequest and Fetch interfaces and adds
            some easy to use features on top of them. By default the Http uses XMLHttpRequest with Promise if supported by browser otherwise the Promise is not used.
            Using the Promise happens under the hood so if the browser does not support the Promise it wont affect to user anyhow and it does not affect on syntax. The user might not even
            notice that where the Http uses the Promise or not.`
        }},
        {'component:text': {text: 
            `In order to use Fetch however the user must explicitly call the Http.fetch().. functions and then the Fetch is used. This is because the Fetch interface is not directly
            compatible with the XMLHttpRequest. Also the Fetch is not supported by older browsers.`
        }},
        {'component:text': {text: 
            `The Http interface by default uses Content-Type application/json. Which means that posted and retrived data is automatically parsed and stringified.
            When an object is given as data parameter it will be stringified by default and response objects will be parsed to JSON objects by default.`
        }},
        {'component:text': {text: 
            `For example. Lets assume that we have some data in a variable called state and we want to posit the data to server. The server will also return the updated data which will then be
            stored into the RME instance storage for later use.`
        }},
        {tptext: {text: 'Using XMLHttpRequest to post the data to server.'}},
        {dcode: {
            es6:
'Http.post(\"/example/data/\", state)\n\
    .then(response => RME.storage(\"data\", response))\n\
    .catch(error => console.log(error));',
            es5:
'Http.post(\"/example/data/\", state)\n\
    .then(function(response) {\n\
        RME.storage(\"data\", response);\n\
    }).catch(function(error) {\n\
        console.log(error);\n\
    });'
        }},
        {tptext: {text: 'The same request using Http.fetch.'}},
        {code: {text:
'Http.fetch().post(\'/example/data/\', state)\n\
    .then(success => {\n\
        if(success.ok)\n\
            success.json();\n\
        else\n\
            throw new Error(success);\n\
    }).then(response => RME.storage(\'data\', response))\n\
        .catch(error => console.log(error));'
        }},
        {tptext: {text: 'Using XMLHttpRequest to get the data from sever.'}},
        {dcode: {
            es6: 
'Http.get(\'/example/data/\')\n\
    .then(response => RME.storage(\'data\', response))\n\
    .catch(error => console.log(error));',
            es5: 
'Http.get(\'/example/data/\')\n\
    .then(function(response) {\n\
        RME.storage(\'data\', response);\n\
    }).catch(function(error) {\n\
        console.log(error);\n\
    });'
        }},
        {tptext: {text: 'The same request using Http.fetch.'}},
        {code: {text:
'Http.fetch().get(\"/example/data/\")\n\
    .then(success => {\n\
        if(success.ok)\n\
            success.json();\n\
        else\n\
            throw new Error(success);\n\
    }).then(response => {\n\
        RME.storage(\'data\', response);\n\
    }).catch(error => {\n\
        console.log(error);\n\
    });'
        }},
    ]
})});

RME.component({ elemsEx: () => ({
    'div.w3-container': [
        {h1: () => "Elements explained"},
        {'component:text': {text: 
            `Elements are renderable objects. Elem class is a wrapper object for HTMLDocument element.
            Tree and Template classes use Elem class. The Elem class makes handling of the
            HTMLDocument elements easier as it offers many easy to use features.`
        }},
        {'component:text': {text: 
            'Creating a new element is easy but when the element is created it is still not attached to the HTMLDocument tree.'
        }},
        {tptext: {
            text: 'Lets create an element div.'
        }},
        {code: {
            text: 'new Elem(\'div\');'
        }},
        {"component:text": {text: 
            'Since the div without content wont be visible we can add an element p inside the div.'
        }},
        {tptext: {
            text: 'Lets create an element p inside the div and give some text for the p.'
        }},
        {code: {text:
'new Elem(\'div\').append(new Elem(\'p\').setText(\'this is a test\'));'
        }},
        {'component:text': {text:
            `The elements needs to be attached to the HTMLDocument tree in order to show up on the HTML page.`
        }},
        {tptext: {
            text: 'Now we attach the div and the p to the Body.'
        }},
        {code: {text:
'Tree.getBody().append(new Elem(\'div\').append(new Elem(\'p\').setText(\'this is a test\')));'
        }},
        {paragraph: [
            {sline: {text: 
                `Eventhough this way to create and attach an element to the Body is relatively simple and nice.
                It still might become quite messy if each element is created and attached like this.
                In order to avoid too many variables and references there is a class Template to come to rescure. More about templates in `
            }},
            {navLink: {
                href: '/rme/templates-explaine',
                text: 'Templates Explained'
            }},
            {sline: {text: '.'}}
        ]},
        {'component:text': {text:
            `All setter methods that does not explicitly return some value
            will return an Elem instance. This enables method chaining and therefore compact code.`
        }},
        {tptext: {
            text: 'Method chaining illustrated.'
        }},
        {code: {text:
'new Elem(\'p\').setId(\'myId\').addClasses(\'class1 class2\').setText(\'Some text here...\');'
        }},
        {subTitle: {text: 'Event handling'}},
        {'component:text': {text: 
            `Elem objects only support one event handling method for each event type that it might support.
            An event handling method will get one parameter that is an event that occured on the element.`
        }},
        {tptext: {text: 
            'Event handler method is created as follows.'
        }},
        {dcode: {
            es6:
'new Elem(\'button\').onClick(event => Elem.wrap(event.target).display(false));',
            es5:
'new Elem(\'button\').onClick(function(event) {\n\
    Elem.wrap(event.target).display(false);\n\
});'
        }},
        {subTitle: {text: 'Extend ES 5'}},
        {'component:text': {text:
            `One very special function of the Elem class is an extend() function. This function does the ES5 standard extension to a current instance of the Elem
            class. This function comes very handy when ES 5 is used and there is a need for some extra functionalities to be added for the Elem class.
            Because of this funtion there is no need to create an another object that wraps the Elem class in order to add new functionalies.
            Instead you may directly add new functionalities to the Elem class.
            However this function is not needed if the ES6 standard JavaScript is used as it already has usable extend functionality.`
        }},
        {'component:text': {text:
            `For example, sometimes we would like to wrap all the logic inside a single component. So that the component offers some functionality functions
            and the component itself will know how to handle data that is given to it. Also the same time the component would be renderable Elem object.
            Keeping this in mind we could create a table element that knows how to handle data by itself.`
        }},
        {tptext: {
            text: 'First we need to create a table element and we also want to exend it in order to give more functionalities for it.'
        }},
        {code: {text:
"var table = new Elem(\"table\").extend(Table);\n\
\n\
function Table {\n\
    this.update = function(data) {\n\
        this.render(createRows(data));\n\
    }\n\
}"
        }},
        {'component:text': {text:
            `Now you may notice that the Table function above is a child class of a super class Elem. Therefore in the Table function we can use all the
            same functions that the Elem class has to offer. The this.render function is the super class function and the createRows() function is a
            custom function that creates new rows for the table Elem to render.`
        }},
        {'component:text': {text:
            `Now we are able to use the Table instance that itself knows how to handle the given data. Each time the data is changed or when it is
            rendered the first time, we could invoke the function update(data) that creates the table rows and then it would invoke the function render from the
            Elem class that will render the created table rows.`
        }},
        {tptext: { text: 
            `Assuming we have data to render the first time or even after the data has changed. 
            We can invoke the Table instance function update that will handle the rest.`
        }},
        {code: {text:
'table.update(dataArray);'
        }},
        {subTitle: {text: 'Render'}},
        {'component:text': {text: 
            `One more thing to mention about is the render() function. You should not confuse this function to React.render or
            some other JavaScript framework that does render from top to bottom.
            The render function of the Elem class simply removes old state form an element and then appends new state into the element. 
            If the state is not changed then it wont be re-rendered.
            The render function should be used wisely as it is heavier operation than append or remove.
            It can be used in almost all circumstances but it does not suit in all situations.
            Many times use of append or remove is more than enough. The render function suits to dynamically change content of a single container.
            Also the render function only renders other Elem objects.`
        }},
        {'component:text': {text:
            'Lets assume we have an element ul that renders li elements.'
        }},
        {tptext: {
            text: 'Lets first create an array of li elements.'
        }},
        {code: {text:
'let arrayOfLiElems = [new Elem(\'li\').setText(\'text 1\'), new Elem(\'li\').setText(\'text 2\'),....];'
        }},
        {tptext: {
            text: 'Then we create an ul element and first time render the elements.'
        }},
        {code: {text:
'let ul = new Elem(\'ul\').render(arrayOfLiElems);'
        }},
        {'component:text': {
            text: 'Now lets assume that as a result of some action content of the array of li elements has changed.'
        }},
        {tptext: {text:
            'Then we call the render function again and the changed content will show up on a page inside the element.'
        }},
        {code: {text:
'ul.render(arrayOfLiElems);'
        }},
    ]
})});