RME.component({ messagesEx: () => ({
    'div.w3-container': [
        {h1: () => 'Messages Explained'},
        {'component:text': {text: 
            `Messages class handles internationalization. The class offers public methods that enable easy using of translated content.
            The Elem and the Template classes have the Messages functionality integrated.`
        }},
        {'component:text': {text:
            `Usage and initialization of the Messages class is relatively simple and straightforward.
            In order not to hard code the default locale of the webpage, we could fist ask the server for a current locale. 
            When the locale is returned from the server. Then simply set the locale as the current locale.
            After this we could load or choose the translated messages to use.`
        }},
        {tptext: {text: 
            "Following code clip could ask for a current locale from server and then set it to be used in the Messages class."
        }},
        {code: {text: 
'Http.get(\'/lang/\').then(response => Messages.lang(response)});'
        }},
        {'component:text': {text: 
            `Messages.load function is invoded right after the locale is set by invoking the Messages.lang function. 
            The load handler receives two parameters from which the first is the locale that was given by the function Messages.lang.
            The second parameter is a function setMessages that receives either an array or an object of the next messages to be used.`
        }},
        {tptext: {text: 
            'Code clip below asks the server for the translated messages for the given locale and then uses the setMessage function to set the messages.'
        }},
        {dcode: {
            es6:
'Messages.load((locale, setMessages) => {\n\
    Http.get(locale).then(response => setMessages(response));\n\
});',                
            es5: 
'Messages.load(function(locale, setMessages) {\n\
    Http.get(locale)\n\
        .then(function(response) {\n\
            setMessages(response);\n\
        });\n\
});'
        }},
        {tptext: {text: 
            'Assuming that we already had loaded all the messages in advance then the load function could be something like this.'
        }},
        {code: {text: 
'Messages.load((locale, setMessages) => setMessages(RME.storage(locale)));'
        }},
        {subTitle: {text: 'Messages lifecycle'}},
        {"component:text": {text:
            'Lifecycle of the Messages is relatively simple.'
        }},
        {tptext: {text:
            'Ivoking a function Messages.lang(locale)'
        }},
        {code: {text: 
'Messages.lang(localeString|event);'
        }},
        {'component:text': {text: 'If a given parameter is an event then event.preventDefault() is invoked and a locale is ought resolve from a target of the event.'}},
        {tptext: {text: 
            'Will automatically invoke a loader function that is defined inside the Messages.load((locale, setMessages) => {..}).'
        }},
        {code: {text: 
"Messages.load((locale, setMessages) => setMessages(..)));"
        }},
        {tptext: {text:
            'Invoking setMessages function will automatically change language of translated elements.'
        }},
        {subTitle: {text: 'Supported messages structure'}},
        {'component:text': {text: 
            `The Messages class supports two kinds of message structures. One is an object that contains key value pairs and the second is an array that contains
            objects that have key value pair.`
        }},
        {tptext: {text: 'The object structure'}},
        {code: {text:
'var messages_en_obj = {\n\
    \'page.hello.world\': \'Hello World\',\n\
    \'page.sample.text1\': \'Example text1\',\n\
    \'page.sample.text2\': \'Here is a long text\',\n\
    \'page.param.example\': \'Hello {0} {1}, Welcome {2}\'\n\
}'
        }},
        {tptext: {text: 'The array structure'}},
        {code: {text:
'var messages_en = [\n\
    {\'page.hello.world\': \'Hello World\'},\n\
    {\'page.sample.text1\': \'Example text1\'},\n\
    {\'page.sample.text2\': \'Here is a long text\'},\n\
    {\'page.param.example\': \'Hello {0} {1}, Welcome {2}\'}\n\
];'
        }},
        {'component:text': {text:
            `Translated content also supports message parameters. The parameters are marked with an index number starting from
            zero (0) inside the curly braces e.g. {0}.`
        }},
        {subTitle: {text: 'Using the Messages with the Elem and the Template'}},
        {'component:text': {text: 
            'The Elem and the Template objects have integrated translated messages capability inside.'
        }},
        {tptext: {text: 'Templates have the message attribute for the translated content.'}},
        {code: {text: 
'Template.resolve({\n\
    p: {\n\
        message: \"page.hello.world\"\n\
    }\n\
});'
        }},
        {tptext: {text: 'The code above would result to.'}},
        {code: {text: 
            '<p>Hello World</p>'
        }},
        {'component:text': {text: 
            'Now lets try using the message parameters.'
        }},
        {code: {text: 
'Template.resolve({\n\
    p: {\n\
        message: \"page.param.example{sir}{Richard}{to Finland.}\"\n\
    }\n\
});'
        }},
        {tptext: {text: 'The code above would result to.'}},
        {code: {text: '<p>Hello sir Richard, Welcome to Finland.</p>'}},
        {line: {}},
        {'component:text': {text:
            'We can also use the message keywords when using function in the template.'
        }},
        {tptext: {text: 
            'Now let\'s define a translated template.'
        }},
        {code: {text:
'Template.resolve({\n\
    p: () => \'page.param.example{sir}{Richard}{to Finland.}\'\n\
});'
        }},
        {tptext: {text: 
            'The code above will result to.'
        }},
        {code: {text:
'<p>Hello sir Richard, Welcome to Finland.</p>'
        }},
        {line: {}},
        {'component:text': {text:
            'We could use the translated content on the Elem object also but usually this is not the case.'
        }},
        {tptext: {text: 'Assuming that the messages above are in use. We could create an element that would print the translated content found by a message key.'}},
        {code: {text: 
'new Elem(\'p\').message(\'page.hello.world\');'
        }},
        {tptext: {text: 'The code clip above would cause the following element be created.'}},
        {code: {text: 
'<p>Hello World</p>'
        }},
        {'component:text': {text: 'Lets try the same using the message parameters.'}},
        {code: {text: 
'new Elem(\'p\').message(\'page.param.example\', \'sir\', \'Richard\', \'to Finland.\');'
        }},
        {tptext: {text: 'The code above would result to.'}},
        {code: {text: 
'<p>Hello sir Richard, Welcome to Finland.</p>'
        }}
    ]
})});