RME.component({ appsEx: () => ({
    'div.w3-container': [
        {h1: () => 'Application Explained'},
        {Text: {text: 
            `Since RME version 1.2.0 onwards the RME has the App class which begins the RME application. The App class should
            be used to create a RME application as it enables automatic shadow tree rendering.`
        }},
        {tptext: {text: 'Let\'s create a default application.'}},
        {code: {text: 
'App.create({\n\
    div: {\n\
        span: () => \'Hello World\'\n\
    }\n\
});'
        }},
        {paragraph: [
            {S: {text: 
            `Above code example will create a default application, that is automatically inserted inside the Body.
            The Body should not have other child elements otherwise it might lead to weird behaviour when the app is re-rendering.
            In order to avoid such cases let's create a default app with specified root element.`}},
            {B: {text: ` Root element should always be empty.`}}
        ]},
        {tptext: {text: `Example HTML page.`}},
        {code: {text:
'<!DOCTYPE html>\n\
<html>\n\
<head>\n\
    <script src="rme.es5.min.js"></script>\n\
    <title>App page</title>\n\
</head>\n\
<body>\n\
    <div id="app"></div>\n\
</body>\n\
</html>'
        }},
        {tptext: {text: `Default application with specified root element.`}},
        {code: {text:
'App.root(\'#app\').create({\n\
    div: {\n\
        span: () => \'Hello World\'\n\
    }\n\
});'
        }},
        {Text: {text:
            `The above application would automatically be inserted inside the div with an id "app".`
        }},
        {subTitle: {text: 'Named Application'}},
        {paragraph: [
            {S: {text: 
            `Named applications are applications that have a name. Naming applications comes handy if
            a page contains multiple applications. It is possible to create and have multiple applications
            on the same page. `}},
            {B: {text: `Though an application inside an application is not recommended and might result to weird 
            behaviour.`}}
        ]},
        {tptext: {text: 'Named application example'}},
        {code: {text: 
'App.name(\'demoApp\').root(\'#demo\').create({\n\
    div: {\n\
        h1: () => \'Demo app\'\n\
    }\n\
});'
        }},
        {subTitle: {text: 'Application components'}},
        {paragraph: [
            {S: {text: `Application components are statefull components as they have state attached
            to them.`}}
        ]},
        {tptext: {text: `Example creates a statefull component for the default app`}},
        {code: {text: 
'App.component({defaultAppComponent: props => ({\n\
    div: {\n\
        text: \'Some Text\'\n\
    }\n\
})})();'
        }},
        {paragraph: [
            {S:{text: `When appName is not defined `}},
            {B: {text: '..();'}},
            {S: {text: ` then the component is created for the default application.`}}
        ]},
        {tptext: {text: `Example creates a statefull component for the demoApp`}},
        {code: {text:
'App.component({demoComponent: props => ({\n\
    div: {\n\
        text: \'Some Text\'\n\
    }\n\
})})(\'demoApp\');'
        }},
        {paragraph: [
            {S: {text: 'When appName is given '}},
            {B: {text: '..(\'demoApp\');'}},
            {S: {text: ` then the component is created for the particular application.`}}
        ]},
        {paragraph: [
            {S: {text: 'More about how components work at: '}},
            {navLink: {href: '/rme/components-explained', text: 'Components Explained'}},
            {S: {text: '.'}}
        ]},
        {subTitle: {text: 'Application state'}},
        {Text: {text: `Application is re-rendered after each state update.`}},
        {tptext: {text: `Get state of a component from the default app.`}},
        {code: {text: 'App.getState(\'compRefName\');'}},
        {tptext: {text: `Set state for a component on the default app.`}},
        {code: {text: 'App.setState(\'compRefName\', state);'}},
        {code: {text: 'App.setState({compRefName: state, otherRefName: otherState});'}},
        {tptext: {text: `Get state of a demoComponent from the named app "demoApp".`}},
        {code: {text: 'App.get(\'demoApp\').getState(\'demoComponent\');'}},
        {tptext: {text: 'Set state of a demoComponent on the anmed app "demoApp".'}},
        {code: {text: 'App.get(\'demoApp\').setState(\'demoComponent\', state);'}},
        {code: {text: 'App.get(\'demoApp\').setState({demoComponent: state, someOtherComp: otherState});'}}
    ]
})});