RME.component({ text: props => ({
    'p.w3-large': {text: props.text, class: props.color ? props.color : ""}
})});

RME.component({ Text: props => ({'component:text': {text: props.text}}) });

RME.component({ tptext: props => ({
    'component:text.w3-text-teal': {text: props.text}
})});

RME.component({ paragraph: props => ({
    'p.w3-large': {
        class: props.color ? props.color : '',
        span: props.lines
    }
})});

RME.component({ sline: props => ({
    span: {text: props.text}
})});

RME.component({ iline: props => ({
    span: {i: {text: props.text}}
})});

RME.component({ bline: props => ({
    span: {b: {text: props.text}}
})});

RME.component({ S: props => ({sline: {text: props.text}}) });

RME.component({ B: props => ({bline: {text: props.text}}) });

RME.component({ I: props => ({iline: {text: props.text}}) });

RME.component({ Comment: props => ({
    'span.w3-text-green': {
        text: props.text,
        br: {}
    }
})});

RME.component({ SL: props => ({
    span: {
        text: props.text,
        br: {}
    }
})});

RME.component({ subTitle: props => ({
    div: {
        line: {},
        h3: {text: props.text}
    }
})});