App.component({ exampleViewer: (props) => {
    let idTitle = props.title.toLowerCase().replace(/ /g, '');
    let pages = [props.code, props.es6, props.es5].map((content, i) => ({
        'div.w3-container.w3-padding': (div) => {
            i === 0 ? div.setId(idTitle) : undefined
            i > 0 ? div.addClasses('code') : undefined
            i > 0 ? div.setText(content) : undefined
            i === 0 ? div.append(content.call()) : undefined
        }
    }));
    return {
        div: {
            h3: {text: props.title},
            'div.w3-card-4': {
                'div.w3-bar.w3-border-bottom.w3-border-light-gray': ['View', 'ES 6', 'ES 5'].map((title, i) => ({
                    viewerTab: {
                        viewerRef: props.ref,
                        selected: props.selected,
                        text: title,
                        idx: i
                    }
                })),
                'div.w3-container.w3-light-gray': pages[props.selected]
            }
        }
    }
}})();

RME.component({ viewerTab: (props) => ({
    'div.w3-bar-item.w3-button.w3-hover-light-gray': {
        class: props.idx === props.selected ? 'w3-light-gray' : '',
        text: props.text,
        onClick: () => {
            if(props.idx !== props.selected)
                App.setState(props.viewerRef, {selected: props.idx});
        }
    }
})});