RME.component({ httpDoc: () => ({
    'div.w3-container': [
        {h1: () =>  "Http"},
        {'component:text': {text:
            `Http class implements the XMLHttpRequest and Fetch. If the browser supports Promise then promise implementation of the XMLHttpRequest is used otherwise the
            request is made without the Promise. Such cases wont affect on syntax and it does not require any actions from the user.
            However in order to use Fetch the user must explicitly call Http.fetch()... This is because Fetch interface is quite different to
            the XMLHttpRequest and not all older browsers support Fetch.`
        }},
        {dataTable: {key: 'httpList', data: RME.storage('httpList')}}
    ]
})});

