RME.component({ cookieDoc: () => ({
    'div.w3-container': [
        {h1: () => 'Cookie'},
        {'component:text': {text: 
            'Cookie interface offers an easy way to get, set or remove cookies in application logic. The Cookie interface handles Cookie objects under the hood. The cookie object may hold following values: '
        }},
        {code: {text: 
'{\n\
name: \"name\",\n\
value: \"value\",\n\
expiresDate: \"expiresDate e.g. Date.toUTCString()\",\n\
cookiePath: \"cookiePath absolute dir\",\n\
cookieDomain: \"cookieDomain e.g example.com\",\n\
setSecureBoolean: true|false\n\
}'
        }},
        {'component:text': {text: 
            `The cookie object also has methods toString() and setExpired(). Notice that setExpired() method wont delete the cookie but merely
            sets it expired. To remove a cookie you should invoke remove(name) method of the Cookie interface.`
        }},
        {dataTable: {key: 'cookie', data: RME.storage('cookieList')}},
        {br: {}},
        {subTitle: {text: 'CookieObject'}},
        {dataTable: {key: 'cookie2', data: [{name: 'toString()', description: 'Retuns string presentation of the cookie object.'}, {name: 'setExpired()', description: 'Sets the cookie expired. Will not delete the the cookie.'}]}}
    ]
})});

