            title: 'Todo List Example',
            code: () => {
                let list = [{li: {text: 'What should I do today?'}}];
                return {
                    div: {
                        'input[placeholder=Type & Press Enter to Add]': {onKeyDown: (event) => {
                            if(event.key === Key.ENTER) {
                                list.push({li: {text: event.target.value}});
                                event.target.value = "";
                                App.setState({lister: {list: list}});
                            }
                        }},
                        lister: {list: list}
                    }
                }
            },
