App.component({ lister: (props) => ({
    ul: props.list
})})();

RME.component({ form: (props) => ({
    div: {
        "label[for=fname]": {
            text: "First name"
        },
        "input#fname[type=text][name=fname][placeholder=First name]": {
            onInput: props.input
        },
        br: {},
        "label[for=lname]": {
            text: "Last name"
        },
        "input#lname[type=text][name=lname][placeholder=Last name]": {
            onInput: props.input
        }
    }
})});

App.component({ statefulHeader: (props) => ({
    h5: () => `Welcome ${props.fname} ${props.lname}`
})})();

App.component({ fTable: props => {
    let borders = {border: '1px solid #000000', borderCollapse: 'collapse'};
    return Template.resolve({
        table: props.data.map(val => ({
            tr: [
                {td: {text: val.abr, styles: borders}},
                {td: {text: val.name, styles: borders}}
            ]
        }))
    }).setStyles(borders);
}})();

