RME.component({simpleExample: () => ({
    'div.w3-container': [
        {h1: () => 'Put things together'},
        {Text: {text: 
            `Let's create a small example application where we put basic aspects of the RME library together.`
        }},
        {tptext: {text: 'Example index.html.'}},
        {code: {text: 
'<!DOCTYPE html>\n\
<html>\n\
<head>\n\
    <script src="rme.es5.min.js"></script>\n\
    <title>App page</title>\n\
</head>\n\
<body>\n\
    <div id="app"></div>\n\
</body>\n\
</html>'
        }},
        {tptext: {text: 'Example index.js.'}},
        {code: [
            {Comment: {text: '//First lets create a default application.'}},
            {SL: {text: 
'App.root(\'#app\').create({\n\
    div: {\n\
        \'div.menu\': [\n\
            {a: {href: \'en\', text: \'English\', onClick: Messages.lang}},\n\
            {a: {href: \'fi\', text: \'Suomi\', onClick: Messages.lang}},\n\
            {a: {href: \'/\', text: \'Page 1\', onClick: Router.navigate}},\n\
            {a: {href: \'/page2\', text: \'Page 2\', onClick: Router.navigate}}\n\
        ],\n\
        \'div#root\': {}\n\
    }\n\
});'
            }},{br: {}},
            {Comment: {text: `//Tell the RME that we want to use these components together.`}},
            {span: () => 'RME.use({'},{br: {}},
            {span: () => '    router: Router,'},{'span.w3-text-green': () => `      //Give static reference`},{br: {}},
            {span: () => '    messages: Messages,'},{'span.w3-text-green': () => `  //Give static reference`},{br: {}},
            {span: () => '    app: App.get(),'},{'span.w3-text-green': () => `      //Give default app instance`},{br: {}},
            {span: () => '});'},{br: {}},
            {br: {}},
            {Comment: {text: `//Then let's create a router.`}},
            {SL: {text:
'Router.url(true).root(\'#root\').routes([\n\
    {route: \'/\', elem: \'Page1\'},\n\
    {route: \'/page2\', elem: \'Page2\'}\n\
]);'
            }},{br: {}},
            {Comment: {text: `//For this example we hard code some example messages.`}},
            {SL: {text:
'RME.storage(\'en\', {\n\
    \'message.hello.world\': \'Hello World Page 1\',\n\
    \'message.page2\':\'Page 2\'\n\
});'
            }},
            {SL: {text: 
'RME.storage(\'fi\', {\n\
    \'message.hello.world\': \'Terve Maailma Sivu 1\',\n\
    \'message.page2\':\'Sivu 2\'\n\
});'
            }},{br: {}},
            {Comment: {text: '//Then define a message loader function.'}},
            {SL: {text: 'Messages.load((locale, setMessages) => setMessages(RME.storage(locale)));'}},
            {Comment: {text: '//And load the messages.'}},
            {SL: {text: 'Messages.lang(\'en\');'}},{br: {}},
            {Comment: {text: `//Now we create page components for the router.`}},
            {SL: {text: `RME.component({Page1: () => ({div: {span: () => \'message.hello.world\'}}) });`}},
            {SL: {text: `RME.component({Page2: () => ({div: {span: () => \'message.page2\'}}) });`}}
        ]},
    ]
})});