RME.component({ components: () => ({
    'div.w3-container': [
        {h1: () => 'Components explained'},
        {'component:text': {text: 
            `Components are resusable functions that are stored in to the RME instance by a name. The components might be an UI component or implement any kind of application logic.
            Components are encouraged to have an unique name definition as latter defintion of the same name overrides previous.`
        }},
        {paragraph: [
            {sline: {text: 'Moreover if component name equals to the HTML 5 tag name or an attribute name then the component must be explicitly invoked when using components with templates. See '}},
            {navLink: {href: '/rme/templates-explained', text: 'Templates Explained'}},
            {sline: {text: '.'}}
        ]},
        {tptext: {text: 
            'Stateless components are created by invoking a RME.component({..}); function.'
        }},
        {dcode: {
            es6: 
'RME.component({ statelessComponent: props => ({\n\
    div: {span: () => \'Hello World\'}\n\
})});',
            es5:
'RME.component({ statelessComponent: function(props) {\n\
    return {div: {span: function() {return \'Hello World\'}}}\n\
}});'
        }},
        {tptext: {text: 
            'Statefull components are created by invoking a App.component({..})(appName); function.'
        }},
        {dcode: {
            es6:
'App.component({ statefullComponent: props => ({\n\
    div: {span: () => \'Hello World\'}\n\
})})();',
            es5:
'App.component({ statefullComponent: function(props) {\n\
    return {div: {span: function() {return \'Hello World\'}}}\n\
}})();'
        }},
        {paragraph: [
            {sline: {text: 
                `In an example above notice that there is not appName filled in instead the name is left empty. Reason being
                that when the appName is not filled in the default app is used. More about Applications at `}},
            {navLink: {href: '/rme/app-explained', text: 'Application Explained'}},
            {sline: {text: '.'}}
        ]},
        {tptext: {text: 
            'Examples above would produce following.'
        }},
        {code: {text:
'<div>\n\
    <span>Hello World</span>\n\
</div>'
        }},
        {subTitle: {text: 'Stateless components & properties'}},
        {tptext: {text: 'Assume that we have a component used in a template and we pass properties to the component.'}},
        {code: {text: 
'...{awesomeButton: {\n\
    text: \'AwesomeButton\',\n\
    idx: 0\n\
}}....'
        }},
        {tptext: {text: 'Stateless component receiving properties.'}},
        {dcode: {
            es6:
'RME.component({ awesomeButton: props => ({\n\
    \'div.button\': {\n\
        text: props.text,\n\
        onClick: (event) => console.log(\`Clicked button ${props.idx}\`)\n\
    }\n\
})});',
            es5:
'RME.component({ awesomeButton: function(props) {\n\
    return {\n\
        \'div.button\': {\n\
            text: props.text,\n\
            onClick: function(event) {\n\
                console.log(\'Clicked button \' + props.idx);\n\
            }\n\
        }\n\
    }\n\
}});'
        }},
        {subTitle: {text: 'Statefull components & properties'}},
        {paragraph: [
            {S: {text: `Statefull components have some predefinitions which are `}},
            {B: {text: 'key'}},{S: {text: ' and '}},{B: {text: 'ref'}},{S: {text: '.'}},
        ]},
        {'ul.w3-large': [
            {li: [
                {B: {text: 'key'}},{S: {text: ' optional string value that will make the component state reference unique.'}}
            ]},
            {li: [
                {B: {text: 'ref'}},{S: {text: ' reference to the component state, is component name + key value.'}}
            ]}
        ]},
        {paragraph: [
            {S: {text: 'If a key is given to the statefull component then its state is made unique by the key value. As by default the component state is referenced by the name of the component.'}}
        ]},
        {tptext: {text: 'Here is a simple example'}},
        {code: {text: 
'...{tabber: {\n\
    key: \'stateRef\',\n\
    tabs: [\'one\',\'two\'],\n\
    selected: 0\n\
}}'
        }},
        {tptext: {text: 'Statefull component by design receives a property "ref" that is the reference to the component state.'}},
        {dcode: {
            es6:
'App.component({tabber: props => ({\n\
    div: props.tabs.map((tab, i) => ({\n\
        awesomeTab: {\n\
            tabberRef: props.ref,\n\
            name: tab,\n\
            idx: i,\n\
            selected: props.selected\n\
        }\n\
    }))\n\
})})();',
            es5: 
'App.component({tabber: function(props) {\n\
    return {\n\
        div: props.tabs.map(function(tab, i) {\n\
            return {\n\
                awesomeTab: {\n\
                    tabberRef: props.ref,\n\
                    name: tab,\n\
                    idx: i,\n\
                    selected: props.selected\n\
                }\n\
            }\n\
        });\n\
    }\n\
}})();'
        }},
        {Text: {text: 'In above example we pass the tabber component reference to its child component. This way the child component will know to which parent component it belongs and which state to manipulate.'}},
        {tptext: {text: 'AwesomeTab component could be something like this.'}},
        {dcode: {
            es6: 
'RME.component({awesomeTab: props => ({\n\
    \'div.tab\': {\n\
        class: props.selected === props.idx ? \'selectedTab\' : \'\',\n\
        text: props.name,\n\
        onClick: (event) => props.selected !== props.idx\n\
            ? App.setState(props.tabberRef, {selected: props.idx})\n\
            : undefined\n\
    }\n\
})});',
            es5: 
'RME.component({awesomeTab: function(props) {\n\
    return {\n\
        \'div.tab\': {\n\
            class: props.selected === props.idx ? \'selectedTab\' : \'\',\n\
            text: props.name,\n\
            onClick: function(event) {\n\
                props.selected !== props.idx\n\
                    ? App.setState(props.tabberRef, {selected: props.idx})\n\
                    : undefined\n\
            }\n\
        }\n\
    }\n\
}});'
        }},
        {Text: {text: 
            `The child component does not need to be a statefull component eventhough the parent component is. Infact it is better design that component state is
            kept in the parent component.`
        }},
        {paragraph: [
            {S: {text: 
                `The tabber and the awesomeTab example life cycle would be following: The tab is clicked -> if not selected -> update the tabber state with newly selected index ->
                application re-renders -> the awesomeTab component receives new properties -> the new properties contain newly selected index -> the tab is rendered selected according the received selected index.`
            }}
        ]},
        {subTitle: {text: 'RME.component get component usage'}},
        {tptext: {text: 
            'Components can also be retrieved using RME.component function though it is not usual to do so.'
        }},
        {code: {text: 
            'RME.component(\'someComponent\', {prop1: \'some prop1\', prop2: \'some prop2\'});'
        }},
    ]
})});